.app.LEADERBOARD .page__content{position:relative;padding-top:0}.leaderboard{width:100%;margin-left:auto;margin-right:auto}.leaderboard__description{color:#f2f2f3;line-height:1.4}.leaderboard__description p{margin-bottom:10px}.leaderboard__description .is-highlight{padding-left:10px;border-left:2px solid #ff6e09}.leaderboard__description a{color:#ff6e09}.leaderboard__filters{margin-top:20px}.leaderboard__filters p{display:inline-block;margin-right:10px}.leaderboard__filters label{display:inline;color:#e4e5e7;font-size:1rem;margin-right:10px}.leaderboard__entries{margin:20px auto 0}.leaderboard__entries th{text-align:left;font-weight:700;padding:10px 0}.leaderboard__entries th:first-child,.leaderboard__entries th:last-child{text-align:center}.leaderboard .xAxis{display:none!important}.entry{color:#e4e5e7}.entry td{vertical-align:middle}.entry td:first-child,.entry td:last-child{text-align:center}.entry__image{width:32px;height:32px;margin-right:20px;background-color:hsla(0,0%,6%,.5)}.entry__info{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;margin:10px 0;color:#e4e5e7}.entry__name{font-size:1.125em;display:block;line-height:1;-ms-flex:auto;flex:auto}.entry__rating{font-size:1.5rem;-ms-flex:none;flex:none}.entry.is-top .playercard-name{font-size:1.25rem}.entry.is-top .playercard-image{width:80px;height:80px}#chanky{width:30px;position:absolute;left:30px;bottom:0}@media screen and (min-width:800px){.entry__image{width:48px;height:48px}.entry__rating{font-size:2.5rem}}